

@media screen and (max-width: 768px) {

#evacuation-page {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: absolute;
    z-index: 2000;
    text-align: center;
    top: 57%;
    left: 50%;
    transform: translate(-50%);
    background-color: #d1d1d1;
    width: 100vw;
    min-height: 110vh; /*   */
    direction: rtl;
    height: auto; /*    */
}

.evacuation-title {
    font-family: "Karantina";
    color: #6a6f77;
    font-size: 3.2em;
    padding: 1vh;
    font-weight: 500;
    margin-top: 10vh;
    position: relative; /*  -absolute -relative */
    width: 100%; /*  -100vw -100% */
    text-align: center;
}

.icon-evacuation {
    position: absolute; /*  -absolute -relative */
    margin-top: 12vh; /*    */
    margin-right: 20vw;
    height: 5vh;
    width: auto;
}

.tooltip {
    margin-right: 10vw;
}

.tooltip-title {
    display: flex;
    align-items: flex-end;
    font-weight: bold;
    direction: rtl;
    font-family: 'heebo';
    color:#6a6f77;
    font-size: 1.5em;
  
}

.chevron {
    margin-right: 2vw;
    transform: rotate(180deg);
    height:4vh;
    width: auto;
}

.chevron.expanded {
    transform: rotate(90deg); /*    */
}

.tooltip-content {
    min-width: 80vw;
    font-family: "Heebo";
    font-size: 1.2em;
    max-width:100vw;
    padding: 10px;
    border-radius: 5px; /*   */
    margin-right: 2vw;
    margin-left: 10vw; /*     */
}


ul {
    padding-left: 20px; /*      */
}
.checkbox-item {
    display: flex; 
    align-items: start; 
    margin-bottom: 5px; /*     */
}

.checkbox-text {
    margin-left: 10px; /*    '   */
}


.mine-checkbox {
    width: 5vw;
    height: 5vw;
    border: 2px solid black;
    border-radius: 0;
    appearance: none;
    cursor: pointer;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
    margin-right: 0vw;
    position: relative;
    padding: 7px;
}

/*   -checkbox    */
.mine-checkbox:checked {
    background-color: black; /*     */
    border-color: black; /*      */
}

/*   V  -checkbox  */
.mine-checkbox:checked::before {
    content: ''; /*  V */
    position: absolute; /*   */
    color: white; /*   */
    left: 50%; /*   -  */
    top: 50%; /*   -  */
    font-size: 2.5vw; /*   */
    line-height: 0; /*    -checkbox */
    transform: translate(-50%, -50%); /*   */
    font-weight: 700;
    display: flex; /*      */
    justify-content: center; /*   */
    align-items: center; /*   */
}

/*    */
.tasks-list {
    list-style: none; /*    */
    padding: 0; /*    */
    margin: 0; /*    */
}
.tooltips-container {
    margin-left: 20px; /*     */
    right: 5px;
}

.title-checked::before{
    content: '';
    color: rgb(34, 181, 115);
}
.title-checked{
    color: rgb(34, 181, 115);
}
}

 
